.header {
  max-width: 880px;
  width: 68.75%;
  min-height: 74px;
  border-bottom: solid 1px rgba(84, 84, 84, 0.7);
  flex-shrink: 0;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 768px) {
  .header {
    width: 80%;
    min-height: 66px;
  }
}

@media screen and (max-width: 467px) {
  .header {
    width: 100%;
    min-height: 58px;
  }
}
